const state = {
  isSimplified: true,
  simplifiedChars: "",
  traditionalChars: "",
};

const mutations = {
  SWITCH_LANGUGE(state, b) {
    state.isSimplified = b;
  },
};

const actions = {
  translateText({state, commit}, strs) {
    var k = '';
    if (state.isSimplified) {
      for (var i = 0; i < strs.length; i++) {
        let tempChar = strs.charAt(i);
        let tempIndex = state.simplifiedChars.indexOf(tempChar);
        k += (tempIndex === -1) ? tempChar : state.traditionalChars.charAt(tempIndex);
      }
    } else {
      for (var i = 0; i < strs.length; i++) {
        let tempChar = strs.charAt(i);
        let tempIndex = state.traditionalChars.indexOf(tempChar);
        k += (tempIndex === -1) ? tempChar : state.simplifiedChars.charAt(tempIndex);
      }
    }
    return k;
  },

  switchLangugeFlag({state, commit}) {
    commit('SWITCH_LANGUGE', !state.isSimplified);
  },
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



