//axios
import MainService from '../libs/server-config';
import qs from 'qs';
import Vue from 'vue';
import store from '../store/index'

//AJAX
MainService.ajax.defaults.headers.common = {
  'X-Requested-With': 'XMLHttpRequest'
}

//   
MainService.ajax.interceptors.request.use(config => {
  if (config.isLoading) {
    //loading(isLoadingaxios)
    store.dispatch('loading/openLoading')
  }

  //token
  return config;
}, error => {
  return Promise.reject(error);
});

// 
MainService.ajax.interceptors.response.use(response => {
  closeLoading();
  return response;
}, error => {
  switch (error.response.status) {
    case 403:

      break;
  }
  console.log(error.response);
  let errMsg = ' ' + error.response.data.error_code + ' ' + error.response.data.msg;
  Vue.prototype.$message({
    message: errMsg,
    type: 'error'
  });

  closeLoading();
  return Promise.reject(error);
});

//loading
function closeLoading() {
  setTimeout(() => {
    store.dispatch('loading/closeLoading')
  }, 100)
}

export default {
  post(url, params = {}) {
    const {username} = params;
    const {isLoading = true} = params;

    return MainService.ajax({
      method: 'post',
      url: url,
      data: qs.stringify(params),
      timeout: 30000,
      auth: {
        username
      },
      isLoading,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      },
    })
  },

  get(url, params = {}) {
    const {username} = params;
    const {isLoading = true} = params;

    return MainService.ajax({
      method: 'get',
      url: url,
      params,
      auth: {
        username
      },
      isLoading,
    })
  },

  delete(url, params = {}) {
    let {isLoading = true} = params;
    return MainService.ajax({
      method: 'delete',
      url: url,
      params,
      isLoading,
    })
  },

  put(url, params = {}) {
    let {isLoading = true} = params;

    return MainService.ajax({
      method: 'put',
      url: url,
      data: qs.stringify(params),
      isLoading,
      timeout: 30000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      },
    })
  },
}



